import { Button, Card, Form, Input, Select } from 'antd';
import { useEffect, useState } from 'react';
import { RiCoinsLine, RiExchangeFill } from 'react-icons/ri';
// import { FaExchangeAlt } from 'react-icons/fa'

const firstDefaultSelectValue = 'Bitcoin';
const secondDefaultSelectValue = 'Ether'

const Converter1 = () => {

    const [cryptolist, setCryptolist] = useState([]);
    const [inputValue, setInputValue] = useState("0");
    const [firstSelect, setFirstSelect] = useState(firstDefaultSelectValue);
    const [secondSelect, setSecondSelect] = useState(secondDefaultSelectValue);
    const [result, setResult] = useState('0');
    const apiUrl = `https://api.coingecko.com/api/v3/exchange_rates`;

    // const names = [{ value: 'jack', label: 'Jack' },
    // { value: 'lucy', label: 'Lucy' },
    // { value: 'Yiminghe', label: 'yiminghe' },
    // { value: 'disabled', label: 'Disabled' }]

    useEffect(() => {
        fetchapi();
    }, [])
    async function fetchapi() {
        const responsedata = await fetch(apiUrl);
        const jsonData = await responsedata.json();
        const data = jsonData.rates;
        // console.log(data);
        // console.log(Object.entries(data));

        const tempArray = Object.entries(data).map((e) => {
            return {
                value: e[1].name,
                label: e[1].name,
                rate: e[1].value
            }
        })
        // console.log(tempArray);
        setCryptolist(tempArray);


    }

    useEffect(() => {
        if (cryptolist.length == 0) return;
        const firstSelectRate = cryptolist.find((i) => {
            return i.value === firstSelect
        }).rate
        const secondSelectRate = cryptolist.find((i) => {
            return i.value === secondSelect
        }).rate
        // console.log(firstSelectRate, secondSelectRate);
        // console.log(inputValue, firstSelect, secondSelect);

        const resultValue = ((inputValue * secondSelectRate) / firstSelectRate);
        setResult(resultValue);
    }, [inputValue, firstSelect, secondSelect]);
    return (
        <div className='container'>
            <Card className='crypto-card' title={<h1><RiCoinsLine />Crypto Converter</h1>} >
                <Form size='large'>
                    <Form.Item>
                        <Input onChange={(e) => setInputValue(e.target.value)} />
                    </Form.Item>
                </Form>
                <div className='select-box'>
                    <Select on defaultValue={firstDefaultSelectValue} style={{ width: '200px' }}
                        options={cryptolist}
                        onChange={(value) => setFirstSelect(value)}
                    />
                    <RiExchangeFill fontSize={35} />
                    <Select defaultValue={secondDefaultSelectValue} style={{ width: '200px' }}
                        options={cryptolist}
                        onChange={(value) => setSecondSelect(value)}
                    />
                </div>
                <h1>{inputValue} {firstSelect} ={result} {secondSelect} </h1>
            </Card>
        </div>
    )
}

export default Converter1;